import * as React from 'react';
import { useTheme } from '@mui/material/styles';
import Box from '@mui/material/Box';
import OutlinedInput from '@mui/material/OutlinedInput';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Chip from '@mui/material/Chip';

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;
const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      width: 250,
    },
  },
};

const names = [
  'Civil & Structural Concrete',
  'Masonry and Plaster',
  'Architectural and Finishing Works.',
  'Pre Cast.',
  'Pre Engineered Structures.',
  'HVAC, Plumbing and Electrical.',
  'Waterproofing and Insulation.',
  'Human Resource Deployment & Management.',
  
];

function getStyles(name, personName, theme) {
  return {
    fontWeight:
      personName.indexOf(name) === -1
        ? theme.typography.fontWeightRegular
        : theme.typography.fontWeightMedium,
  };
}

export default function MultipleSelectChip({services,setServices,setContractor}) {
  const theme = useTheme();
  

  const handleChange = (event) => {
    
    const {
      target: { value },
    } = event;
    setServices(
      // On autofill we get a the stringified value.
      typeof value === 'string' ? value.split(',') : value,
    );
    setContractor((prev)=>{return {...prev,services:value}})
  };

  return (
    <div  className=" px-3 mt-4" >
      <FormControl sx={{ m: 1, width: "100%" }}>
        <InputLabel id="getServices">Services</InputLabel>
        <Select
          labelId="services"
          
          multiple
          value={services}
          onChange={handleChange}
          input={<OutlinedInput id="select-multiple-chip" label="Services" />}
          renderValue={(selected) => (
            <Box sx={{ display: 'flex', flexWrap: 'wrap', gap: 0.5 }}>
              {selected.map((value) => (
                <Chip key={value} label={value} />
              ))}
            </Box>
          )}
          MenuProps={MenuProps}
        >
          {names.map((name) => (
            <MenuItem
              key={name}
              value={name}
              style={getStyles(name, services, theme)}
            >
              {name}
            </MenuItem>
          ))}
        </Select>
      </FormControl>
    </div>
  );
}